import React from 'react';
import styles from './Location.module.scss';
import { Form, Select } from 'antd';
import { PlusOutlined } from '@ant-design/icons';
import ulyanovskMap from '../assets/ulyanovskMap.jpg';


export default function Location() {
  const { Option } = Select;

  return (
    <div className={ styles.location }>
      <div className={ styles.item }>
        <p className={ styles.label }></p>
        <Form.Item
          name='city'
          rules={[{ required: true, message: ' ' }]}
        >
          <Select
            id='city'
            showSearch
            showArrow={ false }
            allowClear
            clearIcon={ <PlusOutlined className={ styles.clearIcon } /> }
            placeholder='   ...'
            optionFilterProp='children'
            filterOption={(input: string, option: any) =>
              option?.children?.toLowerCase().indexOf(input.toLowerCase()) >= 0
            }
            filterSort={(optionA, optionB) =>
              optionA.children?.toLowerCase().localeCompare(optionB.children?.toLowerCase())
            }
          >
            <Option className={ styles.option } value='1'></Option>
            <Option className={ styles.option } value='2'></Option>
            <Option className={ styles.option } value='3'></Option>
            <Option className={ styles.option } value='4'></Option>
            <Option className={ styles.option } value='5'></Option>
            <Option className={ styles.option } value='6'></Option>
          </Select>
        </Form.Item>
      </div>

      <div className={ styles.item }>
        <p className={ styles.label }> </p>
        <Form.Item
          name='orderPlace'
          rules={[{ required: true, message: '  ' }]}
        >
          <Select
            id='orderPlace'
            showSearch
            showArrow={ false }
            allowClear
            clearIcon={ <PlusOutlined className={ styles.clearIcon } /> }
            placeholder='   ...'
            optionFilterProp='children'
            filterOption={(input: string, option: any) =>
              option?.children?.toLowerCase().indexOf(input.toLowerCase()) >= 0
            }
            filterSort={(optionA, optionB) =>
              optionA.children?.toLowerCase().localeCompare(optionB.children?.toLowerCase())
            }
          >
            <Option className={ styles.option } value='1'>. , 1</Option>
            <Option className={ styles.option } value='2'>. , 25</Option>
            <Option className={ styles.option } value='3'>. , 226</Option>
            <Option className={ styles.option } value='4'>.  , 8</Option>
            <Option className={ styles.option } value='5'>. , 45</Option>
          </Select>
        </Form.Item>
      </div>

      <div className={ styles.mapBlock }>
        <p className={ styles.label }>  :</p>
        <img src={ ulyanovskMap } alt=' ' className={ styles.map }/>
      </div>

    </div>
  )
}